import React, { FC } from 'react';
import TestimonialCard, { TestimonialItem } from '../../common/TestimonialCard';
import { TestimonialsGrid, TestimonialsSection } from './styled';
import { Container } from '../../common/Container/Container';

type Props = {
  testimonials: TestimonialItem[];
  backgroundColor?: string;
};

const Testimonials: FC<Props> = ({ testimonials, backgroundColor }) => {
  return (
    <TestimonialsSection backgroundColor={backgroundColor}>
      <Container>
        <TestimonialsGrid>
          {testimonials.map((testimonial) => (
            <TestimonialCard
              colored
              key={testimonial.authorName + testimonial.authorPosition}
              {...testimonial}
            />
          ))}
        </TestimonialsGrid>
      </Container>
    </TestimonialsSection>
  );
};

export default Testimonials;
