import { Link } from "gatsby";
import styled from "styled-components";
import { colors } from "../../../utils/colors";
import { device } from "../../../utils/layoutBreakpoints";

export const ResourceItem = styled(Link)`
  svg {
    path {
      fill: ${colors.primary.white} !important;
    }
  }

  cursor: pointer;
  svg {
    transition: 0.3s;
  }

  &:hover {
    svg {
      transform: scale(1.2);
    }
  }
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 50px 50px 50px;
  align-items: center;
  justify-items: center;
  & > div:first-child {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
`;

export const Resources = styled.div`
  display: grid;
  gap: 65px;
  grid-template-columns: 1fr;
  grid-auto-rows: 1fr;

  @media ${device.tablet} {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
    margin-bottom: 80px;
  }

  @media ${device.laptop} {
    gap: 40px;
    margin-bottom: 0;

    ${ResourceItem}:nth-child(2) {
      border-left: 1px solid ${colors.secondary.gray300};
      border-right: 1px solid ${colors.secondary.gray300};
    }
  }
`;

export const StyledDevResources = styled.section`
  text-align: center;
  font-size: 0.9rem;
  margin-top: 25px;
  margin-bottom: 50px;
  @media ${device.laptop} {
    margin-top: 25px;
    margin-bottom: 50px;
  }
  h5 {
    font-size: 0.9rem;
  }
`;

export const ResourceTitle = styled.p`
  font-weight: 700;
  font-size: 0.88rem;
  line-height: 1.3rem;
  height: 20px;
`;

export const ResourceText = styled.p`
  font-weight: 400;
  font-size: 0.8rem;
  line-height: 1.1rem;
  width: 100%;
  align-self: flex-start;
  color: #767676;

  @media ${device.mobileL} {
    width: 80%;
  }
`;

export const Content = styled.div`
  padding: 25px 25px;
  display: flex;
  flex-direction: column;
  gap: 100px;

  @media ${device.laptop} {
    padding: 50px 100px;
  }
`;